import React from 'react'

import { FormControlLabel, FormControl, Radio, RadioGroup, Tooltip } from '@mui/material'

import InfoIcon from '@mui/icons-material/Info'

import TaskPlot from '../OptimizedSummary/components/TaskPlot'
import DistributionPlot from './components/DistributionPlot'

import { OptimizationContainer, OptimizationCharts } from '../OptimizedSummary/styled'
import { PrimaryButton } from 'src/styled'
import { len } from '../../../mods/a_utils'

export default function BaselineSummary({ optimizer, optimizationPresets, selectedPreset, selectedResultIndex, handleSelectPreset, handleSelectResultIndex, goNext }) {
  const loadingPreset = Object.keys(optimizationPresets).filter((o) => {
    return optimizationPresets[o]?.optimizer?.optimizing
  }).map((o) => { return o })[0]

  const balancedResults = optimizationPresets['balanced']?.optimizer?.results?.length
  const fastestResults = optimizationPresets['shortest_duration']?.optimizer?.results?.length
  const leastMaxResults = optimizationPresets['least_max_resources']?.optimizer?.results?.length
  const leastStdResults = optimizationPresets['least_std_resources']?.optimizer?.results?.length

  const statData = (title, numbers) => {
    return <div className='stats'>
      <div className='title'>{title}
      </div>
      <div className='figure'>
        <div className='numbers'>{numbers}</div>
      </div>
    </div >
  }

  return <OptimizationContainer>
    <h2>Baseline Summary</h2>
    <div className='summary'>
      {statData('Project Duration (days)', Math.round(optimizer?.baseline_result?.drtn_day))}
      {statData('Resources', Math.round(optimizer?.baseline_result?.rsrc_max))}
      {statData('Tasks', Object.values(optimizer?.baseline_result?.inp?.tasks).length)}
      {statData('Tasks on Critical Path', Math.round(optimizer?.baseline_result?.num_crt))}
    </div >
    {
      optimizer ? <>
        <OptimizationCharts className="multi-charts">
          {optimizationPresets ?
            <div className='full-chart'>
              <div className='section'>
                {loadingPreset ?
                  <h3>Currently optimizing for {optimizationPresets[loadingPreset].label}......</h3>
                  : <h3>Optimization result for {optimizationPresets[selectedPreset].label}</h3>
                }
                <div className='legend'>
                  <div className='item'>
                    <div style={{ width: '14px', height: '14px', background: optimizationPresets[loadingPreset ? loadingPreset : selectedPreset].color, marginRight: '5px', borderRadius: '50%' }}></div>
                    <div>{optimizationPresets[loadingPreset ? loadingPreset : selectedPreset].label}</div>
                  </div>
                  <div className='item'>
                    <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                    <div>Baseline</div>
                  </div>
                  {len(optimizationPresets[loadingPreset ? loadingPreset : selectedPreset].optimizer.results.at(-1)?.inp?.tasks || {}) <= 400 ?
                    <div className='item'>
                      <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                      <div>Critical Path</div>
                    </div>
                    : <></>
                  }
                </div>
                {loadingPreset ?
                  <TaskPlot
                    tasks={optimizationPresets[loadingPreset].optimizer.results.at(-1)?.inp.tasks || []}
                    width='1150'
                    height='400'
                    color={optimizationPresets[loadingPreset].color}
                    baselineTasks={optimizer.baseline_result?.inp.tasks}
                    title='Tasks (Overview)' />
                  : <TaskPlot
                    tasks={optimizationPresets[selectedPreset].optimizer.results.at(selectedResultIndex)?.inp.tasks || []}
                    width='1150'
                    height='400'
                    color={optimizationPresets[selectedPreset].color}
                    baselineTasks={optimizer.baseline_result?.inp.tasks}
                    title='Tasks (Overview)' />
                }
              </div>
            </div>
            : null
          }
        </OptimizationCharts>
        <OptimizationCharts>
          <div className='chart'>
            <div className='section'>
              <h3>Comparison Chart</h3>
              <div className='legend'>
                <div className='item'>
                  <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                  <div>Baseline</div>
                </div>
                {Object.keys(optimizationPresets).map((o) => {
                  return <div key={o} className='item'>
                    <div style={{ width: '14px', height: '14px', background: optimizationPresets[o].color, marginRight: '5px', borderRadius: '50%' }}></div>
                    <div>{optimizationPresets[o].label}</div>
                  </div>
                })}
              </div>
              <DistributionPlot
                results={[...Object.keys(optimizationPresets).filter((o) => {
                  return optimizationPresets[o]?.optimizer?.results.length > 0
                }).map((o) => {
                  return {
                    ...optimizationPresets[o]?.optimizer?.results?.at(-1),
                  }
                }) || [],
                {
                  ...optimizer.baseline_result,
                  option: 'baseline'
                }]}
                optimizer={optimizer}
                optimizationPresets={optimizationPresets}
                selectedPreset={selectedPreset}
                selectedResultIndex={selectedResultIndex}
                k='rsrc_max'
                x_label='Duration (days)'
                y_label='Maximum Resource (units)'
                title={'Maximum of combined resources VS Project duration'}
                onClick={(preset, i) => {
                  handleSelectPreset(preset)
                  handleSelectResultIndex(i)
                }} />
            </div>
          </div>

          <div className='chart'>
            <div className='section'>
              <h3>Optimization Presets</h3>
              <p>These available presets are pre-configured settings that are designed to deliver the best performance or quality for each individual specific use case.</p>
              <p>Please select from one of the following options to generate your optimized report: </p>
              <FormControl component='fieldset'>
                <RadioGroup value={selectedPreset} onChange={(e) => {
                  handleSelectPreset(e.target.value)
                  handleSelectResultIndex(optimizationPresets[e.target.value].optimizer.results.length - 1)
                }}>
                  {Object.keys(optimizationPresets).map((o) => {
                    return <div key={o} className='row'>
                      <FormControlLabel disabled={optimizationPresets[o]?.optimizer?.results?.length === 0} value={o} control={<Radio />} label={optimizationPresets[o].label} />
                      <Tooltip title={<div style={{ fontSize: '13px', fontFamily: 'Inter' }}>{optimizationPresets[o].description}</div>}><InfoIcon /></Tooltip>
                    </div>
                  })}
                </RadioGroup>
              </FormControl>
              <div className='row'>
                <div />
                <PrimaryButton variant='contained' onClick={goNext} disabled={balancedResults === 0 && fastestResults === 0 && leastMaxResults === 0 && leastStdResults === 0}>
                  View Optimized Summary
                </PrimaryButton>
              </div>
            </div>
          </div>
        </OptimizationCharts > </> : ''
    }
  </OptimizationContainer >
}