export async function getAllUsers(callback, errorCallback) {
	return await fetch(`${process.env.REACT_APP_API_URL}/users/`)
}


export async function login(body, callback, errorCallback) {
	return await fetch(`${process.env.REACT_APP_API_URL}/auth/login`, {
		method: 'POST',
		headers: { 'Content-Type': 'application/json' },
		body: JSON.stringify(body)
	})
}

export async function requestResetPassword(body, callback, errorCallback) {
	return await fetch(`${process.env.REACT_APP_API_URL}/auth/request-reset-password`, {
		method: 'POST',
		headers: { 'Content-Type': 'application/json' },
		body: JSON.stringify(body)
	})
}

