import React from 'react'

import ResourcePlot from '../../OptimizedSummary/components/ResourcePlot'
import LossPlot from '../components/LossPlot'
import StepsPlot from '../components/StepsPlot'
import { plot_color_palette } from '../../../../utils/color'

import { OptimizationCharts, OptimizationContainer } from '../../OptimizedSummary/styled'

export default function RawData({ results, loss, selectedResultIndex, handleChooseResult }) {

  return <>
    <OptimizationContainer>
      <OptimizationCharts className='multi-charts'>
        <div className='row'><h2>Optimization Iteration Charts</h2></div>
        <div className='chart'>
          <h4>Project Duration</h4>
          <StepsPlot results={results} name='drtn_day' x_axis x_label='Optimization steps' y_label='Duration (days)' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>

        <div className='chart'>
          <h4>Maximum Labour Resources</h4>
          <StepsPlot results={results} name='rsrc_max' x_axis x_label='Optimization steps' y_label='Maximum Resources' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>

        <div className='chart'>
          <h4>Standard Deviation Labour Resources</h4>
          <StepsPlot results={results} name='rsrc_std' x_axis x_label='Optimization steps' y_label='Standard Deviation' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>

        <div className='chart'>
          <h4>Number of Changed Tasks</h4>
          <StepsPlot results={results} name='num_changed' x_axis x_label='Optimization steps' y_label='Number of changed task' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
        <div className='chart'>
          <h4>Critical Path Tasks</h4>
          <StepsPlot results={results} name='num_crt' x_axis x_label='Optimization steps' y_label='Number of tasks' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
        <div className='chart'>
          <h4>Loss Function (Dimensionless)</h4>
          <StepsPlot results={results} name='loss_sum' x_axis x_label='Optimization steps' y_label='Loss function' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
      </OptimizationCharts>
      <OptimizationCharts className='multi-charts'>
        <div className='row'><h2>Resources Plots</h2></div>
        {Object.values(results[selectedResultIndex]?.inp.rsrcs || {}).map((r,i) => (
          <div key={r.id} className='chart'>
            <h4>{`(${r.type}) ${r.name}`}</h4>
            <ResourcePlot dist={r.dist} title={`(${r.type}) ${r.name}`} color={plot_color_palette.ResourcePlot.allResources[i % plot_color_palette.ResourcePlot.allResources.length]}/>
          </div>
        ))}
      </OptimizationCharts>
      <OptimizationCharts className='multi-charts'>
        <div className='row'><h2>Loss Function Charts</h2></div>
        {/* <p>Loss functions in construction schedules are mathematical models used to determine the cost or schedule impact of deviations from the project plan. These functions help to identify potential project risks and allow for proactive measures to be taken to minimize the impact of delays and budget overruns. Common examples include cost-impact and schedule-impact functions.</p> */}
        <div className='chart'>
          <h4>Loss Function: Tasks Resources</h4>
          <LossPlot results={results} name='loss_task_rsrc' x_label='Duration (days)' y_label='Loss function: task resources' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
        <div className='chart'>
          <h4>Loss Function: Sum</h4>
          <LossPlot results={results} name='loss_sum' x_label='Duration (days)' y_label='Loss function: sum' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
        <div className='chart'>
          <h4>Loss Function: Resources</h4>
          <LossPlot results={results} name='loss_rsrc' x_label='Duration (days)' y_label='Loss function: resources' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
        <div className='chart'>
          <h4>Loss Function: Tasks</h4>
          <LossPlot results={results} name='loss_task' x_label='Duration (days)' y_label='Loss function: task' onClick={handleChooseResult} selectedResultIndex={selectedResultIndex} />
        </div>
      </OptimizationCharts>
    </OptimizationContainer>
  </>
}